<template>
  <div class="col-sm-12 mt-3">
    <iq-card class="iq-card">
      <div class="iq-card-body p-3">
        <div class="image-block text-center">
          <img src="@/assets/images/add-user.png" class="img-fluid rounded w-755" alt="blog-img" />
        </div>
      </div>
      <div class="col-12 bg-white pt-2">
        <div class="sign-in-from">
          <h1 class="mb-0 text-center" v-if="user">{{ user.name }}</h1>
          <form class="mt-2">
            <div class="form-group">
              <label for="name">الاسم الجديد</label>
              <input type="text" v-model="v$.form.name.$model" class="form-control mb-0 mt-2" id="name"
                placeholder="ادخل الاسم هنا" />
              <small style="color: red" v-if="v$.form.name.$error">
                ادخل الاسم كما هو في المستند الرسمي
              </small>
            </div>

            <div class="form-group" v-if="msg">
              <small style="color: red">
                {{ msg }}
              </small>
            </div>

            <div class="col-sm-12 text-center" v-if="loader">
              <img src="@/assets/images/page-img/page-load-loader.gif" alt="loader" style="height: 100px;" />
            </div>
            <div class="d-flex align-items-center mt-3 row">
              <div class="d-inline-block w-100 text-center col-12">
                <router-link :to="{ name: 'user.list-all' }" role="button" class="d-block mt-3 mb-3">
                  <span>عودة لقائمة المستخدمين</span>
                  <span class="align-middle material-symbols-outlined">
                    keyboard_return
                  </span>
                </router-link>
              </div>
            </div>

            <div class="d-inline-block w-100">
              <input type="button" @click="onSubmit()" class="btn btn-primary float-end" value="تحديث">

            </div>
          </form>
        </div>
      </div>
    </iq-card>
  </div>
</template>
<script>
import useVuelidate from "@vuelidate/core";
import { required, email } from "@vuelidate/validators";
import userServices from '@/api/userServices'

export default {
  name: "Update User Name",
  setup() {
    return { v$: useVuelidate() };
  },
  async created() {
    this.user = await userServices.getUser(this.$route.params.user_id)
  },
  data() {
    return {
      user: null,
      loader: false,
      form: {
        name: "",
        id: this.$route.params.user_id
      },
      msg: '',
    };
  },
  methods: {
    uploadFile() {

      this.form.file = this.$refs.file.files;
      var re = /(\.jpg|\.jpeg|\.bmp|\.gif|\.png)$/i;
      if (!re.exec(this.form.file[0].name)) {
        this.fileExtnError = 'صيغة الملف غير مدعومة، اختر ملفًا أخر';
      }
      else {
        this.fileExtnError = null
      }

    },
    async onSubmit() {
      this.v$.$touch();

      if (!this.v$.form.$invalid) {
        this.msg = '';
        this.loader = true;
        try {
          const updateInfo = await userServices.updateName(this.form);
          this.loader = false;
          this.msg = ' تم التعديل';

        }
        catch (error) {

          this.loader = false;
          this.msg = ' حدث خطأ';
        }
      }
    },
  },
  validations() {
    return {
      form: {
        name: {
          required,
        },
      },
    };
  },
};
</script>
  
<style lang="scss" scoped>
h1,
h4,
p,
title,
form {
  direction: rtl;
}

.form-check {
  float: left;
}

a.float-end {
  margin-top: 10px;
}
</style>
  